.bike_image{
  width: 70%;
  height: 70%;
  margin: 8% 0 0% 10%;
  
}
.bike_image img {
  width: 90%;
  height: auto;
  margin: 0% 0% 0 10%;
  opacity: 0.9466;
}

@media(max-width:480px){
  .bike_image{
    width: 100vw !important;
    min-height: auto;
    margin: 20% 0 22% -34%;
  }
  .bike_image img{
    width: 170%;
    height: auto;
    object-fit: contain;
    /* transform: rotateZ(90deg); */
    margin: 0% 0% 10% 0%;
    padding-top: 5%;
    padding-left: 0%;
  }
}