
.row {
  margin: 50px 20%;
  margin-bottom: -2%;
}

/* Create two equal columns that sits next to each other */


.galleryContainer{
  /* display: flex;
  flex-wrap: wrap;
  flex-direction: row; */
  /* flex: 33%; */
  margin: 50px 0 0 0%;
  padding: 0 0rem;
}

.row .headStyling{
  padding: 15px 0;
  font-weight: 600;
  color: #f2f2f2;
  /* color: #121212; */
  font-family: 'Montserrat';
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 3em;
  margin: 0 1%;
}

@media(max-width:480px){
  .row{
    margin: 30px 10%;
  }
  .galleryContainer{
    margin: 0px;
    padding: 30px 0rem;
  }
  .row .headStyling{
    font-size: 1.8em;
  }
}