@import url('https://fonts.googleapis.com/css?family=Montserrat:200,300,400,600,800');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html{
  max-width: 100vw;
  height: auto;
  scroll-behavior: smooth;
}

*{
  padding: 0;
  margin: 0;
}
body {
  background-color: #000;
  /* background-color: #fff; */
  background-image: linear-gradient(325deg, #070707 4%, #010101 90% );
  /* background-image: linear-gradient(325deg, #fff 4%, #fcfcfc 90% ); */
  overflow-x: hidden;


}

@media(max-width:480px){
  html, body{
    max-width: 100vw !important;
    overflow-x: hidden;
    scroll-behavior: smooth;
  }
}