.preorder-btn {
  outline: none;
  border: none;
  cursor: pointer;
  background-color: #49D140;
  box-shadow: 6px 3px 15px rgba(210, 210, 210, .1);
  border-radius: 40px;
  font-family: "Rubik", sans-serif;
  font-size: 10pt;
  font-weight: 400;
  /* letter-spacing: 2px; */
  text-transform: uppercase;
  color: #F2F2F2;
  display: inline-block;
  margin: 0% 0 0 33%;
  padding: 20px 80px;
  transition: transform 0.3s ease-out;
  justify-content: center;
  z-index: 1;
  text-align: center;
}

.brochure-btn {
  outline: none;
  border: none;
  cursor: pointer;
  box-shadow: 6px 3px 15px rgba(210, 210, 210, .1);
  border-radius: 40px;
  font-family: "Rubik", sans-serif;
  font-size: 9pt;
  font-weight: 400;
  /* letter-spacing: 2px; */
  text-transform: uppercase;
  color: #F2F2F2;
  /* color: #313131; */
  display: inline;
  background-color: #131313;
  margin: 0% 0 0 5%;
  position: relative;
  padding: 20px 80px;
  transition: transform 0.3s ease-out;
  /* justify-content: center; */
  z-index: 1;
  text-align: center;
}
.preorder-btn:hover, .brochure-btn:hover{
  /* color: #000; */
  color: #fff;
  text-decoration: none;
  box-shadow: 3px 3px 15px rgba(210, 210, 210, .2);
  /* box-shadow: 3px 3px 15px rgba(210, 210, 210, 1); */
}

.preorder-form .preorder-btn:hover ,.preorder-btn:hover {
  background-color: #191919;
  transition: transform 0.3s ease-out;
  border: none;
  /* transform: translate(1px, -1px); */
  box-shadow: 3px 3px 15px rgba(210, 210, 210, .2);

}


